// export default RepaymentHistory;

import SkeletonCustom from "components/Skeleton/Skeleton";
import Text from "components/Text/Text";
import React from "react";

export interface IRepaymentHistory {
  customerName: string;
  Purpose: string;
  "bank.type.name": string;
  bankName: string;
  sanctionedAmount: number | string;
  outstandingAmount: number | string;
  balanceTenor: number | string;
  emiPaid: number | string;
  pastDefaults: number | string;
  sanctionedTenor: number | string;
  noOfBounces: number | string;
  roi: number | string;
}

export interface IProps {
  data: IRepaymentHistory;
}

export interface IComparisonItem {
  label: string;
  key: keyof IRepaymentHistory;
}

const comparisonItems: IComparisonItem[] = [
  { label: "label.comparison.customerName", key: "customerName" },
  { label: "label.comparison.purpose", key: "Purpose" },
  { label: "label.comparison.source", key: "bank.type.name" },
  { label: "label.comparison.nameOfInstitution", key: "bankName" },
  { label: "label.comparison.sanctionAmount", key: "sanctionedAmount" },
  { label: "label.comparison.outStandingAmount", key: "outstandingAmount" },
  { label: "label.comparison.balanceTenor", key: "balanceTenor" },
  { label: "label.comparison.Emi", key: "emiPaid" },
  { label: "label.comparison.PastDefaults", key: "pastDefaults" },
  { label: "label.comparison.sanctionTenor", key: "sanctionedTenor" },
  { label: "label.comparison.noOfBounces", key: "noOfBounces" },
  { label: "label.comparison.ROI", key: "roi" },
];

const RepaymentHistory = (props: any) => {
  const { data, loading } = props;
  if (loading) {
    return (
      <>
        <SkeletonCustom width="100%" height="32.5rem" className="rounded" borderRadius="12px"/>
      </>
    );
  }
  return (
    <div className="flex flex-col w-full m-5">
      <div className="rounded w-full flex flex-row pl-4 py-4 font-bold bg-primary-tableRowColor h-12 border-primary-gray-1000 border-0.5">
        <div className="w-full sm:w-1/2 text-xs text-primary-ptext-1000">
          <Text label={"label.comparison.customerType"} />
        </div>
        <div className="w-full sm:w-1/2 text-xs text-primary-ptext-1000">
          <Text label={"label.comparison.CoApplicant"} />
        </div>
      </div>

      {comparisonItems.map((item, index) => (
        <div
          key={index}
          className={`flex justify-between h-9 items-center border-primary-gray-1000 border-0.5 pl-4 py-4 ${
            index % 2 !== 0 ? "bg-primary-tableRowColor" : ""
          }`}
        >
          <div className="w-full sm:w-1/2 text-xs text-primary-ptext-1000">
            <Text label={item.label} />
          </div>
          <label className="w-full sm:w-1/2 text-xs text-black">{data[item.key] ?? "NA"}</label>
        </div>
      ))}
    </div>
  );
};

export default RepaymentHistory;
