import BaseApiService from "../BaseApiService";

const DEFAULT_PATH = "/custom/api/comparison";
const COMMON_PATH = "/custom/api";

class Comparison extends BaseApiService {
  getAll = (start = 0, limit = 10, search: string) => {
    const endPoint =`${DEFAULT_PATH}/view${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  updateInsuranceDetails = (body: any) => {
    const endPoint = DEFAULT_PATH + `/insurance/details`;
    return this.makePutRequestWithAuth(endPoint, body);
  };

  updateIncomeDocumentList = (start = 0, limit = 10, search: string) => {
    const endPoint = DEFAULT_PATH + `/income/documents/validate${search}`;
    return this.makePutRequestWithAuth(endPoint);
  };

  getLoanTags = (start = 0, limit = 10, search: string) => {
    const endPoint = COMMON_PATH + `/loan/tag/search${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  updateLoanTags = (body: any) => {
    const endPoint = DEFAULT_PATH + `/loan/tag`;
    return this.makePutRequestWithAuth(endPoint, body);
  };

  updateFinancierLoanNumber = (body: any) => {
    const endPoint = DEFAULT_PATH + `/financial/loan/number`;
    return this.makePutRequestWithAuth(endPoint, body);
  };
}

export default new Comparison();
