import SkeletonCustom from "components/Skeleton/Skeleton";
import Text from "components/Text/Text";
import { formatDate } from "utils/CommonUtil";

interface ISalesManager {
  name: string;
}

interface ILoan {
  salesManager?: ISalesManager;
}

export interface IData {
  dataCapturedBy: string;
  designation: string;
  manger: string;
  dateOfInitiation: string;
  createdByUserName: string;
  createdAt: string;
  loan?: ILoan;
}

interface IProps {
  data: IData;
}
const DataCapture = (props: any) => {
  const { data, loading } = props;
  
  if (loading) {
    return <SkeletonCustom width="100%" height="7.8rem" className="rounded" borderRadius="12px" />;
  }
  return (
    <div className="bg-[#FAFAFC] border rounded-xl p-3 flex flex-col gap-5">
      <div className="flex flex-row w-full">
        <div className="flex flex-col gap-1 w-1/2">
          <div className="text-xs text-primary-ptext-1000">
            <Text label={"label.comparison.dataCapturedBy"} />
          </div>
          <label className="text-xs text-black">{data?.createdByUserName ?? "NA"}</label>
        </div>
        <div className="flex flex-col gap-1 w-1/2">
          <div className="text-xs text-primary-ptext-1000">
            <Text label={"label.comparison.designation"} />
          </div>
          <label className="text-xs text-black">{data?.designation ?? "NA"}</label>
        </div>
      </div>
      <div className="flex flex-row w-full">
        <div className="flex flex-col gap-1 w-1/2">
          <div className="text-xs text-primary-ptext-1000">
            <Text label={"label.comparison.manger"} />
          </div>
          <label className="text-xs text-black">{data?.loan?.salesManager?.name ?? "NA"}</label>
        </div>
        <div className="flex flex-col gap-1 w-1/2">
          <div className="text-xs text-primary-ptext-1000">
            <Text label={"label.comparison.dateOfInitiation"} />
          </div>
          <label className="text-xs text-black">{formatDate(data?.createdAt) ?? "NA"}</label>
        </div>
      </div>
    </div>
  );
};

export default DataCapture;
