import SkeletonCustom from "components/Skeleton/Skeleton";
import Text from "components/Text/Text";
import { currencyFormatter } from "locale";
import { getStaticMasterNames } from "utils/CommonUtil";
interface IAsset {
  smAssetType: string;
  assetType: string;
  amount: string;
}
export interface IInvestment {
  financialDetails: any;
  investment: any;
  commentOnInvestment: React.ReactChild | React.ReactChild[]; // Adjust this based on your requirements
  remarksOnInvestment: React.ReactChild | React.ReactChild[]; // Adjust this based on your requirements
  kycList: IAsset[];
  comments: string;
}
interface IProps {
  data: IInvestment;
}
const Investment = (props: any) => {
  const { data, loading } = props;
  if (loading) {
    return (
      <>
        <SkeletonCustom width="100%" height="11rem" className="rounded" borderRadius="12px" />
      </>
    );
  }
  // alert(data.investment[0].amount)
  return (
    <div className="flex flex-col gap-3 m-5">
      <div className="w-full border rounded">
        <div className="rounded w-full flex flex-row px-4 py-4 font-bold bg-primary-tableRowColor">
          <div className="w-1/2 text-xs text-primary-ptext-1000">
            <Text label={"label.comparison.kycType"} />
            {/* check in loan listing */}
          </div>
          <div className="w-1/2 text-xs text-primary-ptext-1000">
            <Text label={"label.comparison.kycNumber"} />
          </div>
        </div>

        {(data?.financialDetails || data.investment).map((item: any) => (
          <div
            className="rounded w-full flex flex-row px-4 py-4 font-bold even:bg-primary-tableRowColor"
            key={item?.smAssetType}
          >
            <div className="w-1/2 text-xs text-primary-ptext-1000">
              <Text label={getStaticMasterNames(item?.smAssetType) ?? "NA"} />
            </div>
            <div className="w-1/2 text-xs text-primary-ptext-1000">
              <Text label={currencyFormatter(item?.amount) ?? "NA"} />
            </div>
          </div>
        ))}
      </div>
      <div className="flex flex-row w-full">
        <div className="flex flex-col gap-1 w-full">
          <div className="text-xs text-primary-ptext-1000">
            <Text label={"label.comparison.comments"} />
          </div>
          <label className="text-xs text-black">
            {data?.commentOnInvestment ?? data?.remarksOnInvestment}
          </label>
        </div>
      </div>
    </div>
  );
};

export default Investment;
